<template>
  <div>
    <v-row class="flex-wrap py-3 px-2 mb-2">
      <v-col class="pa-1 animate__animated animate__fadeIn" cols="12" sm="4" md="3" v-for="(item,i) in files"
             :key="i+'upimage'">
        <div class="up-box">
          <img @click="showImage(item.file)" v-if="isImage(item.file)" :style="[{width : hide?'25%':'15%'}]"
               height="100%" style="object-fit: cover;cursor: pointer" :src="item.file">
          <a target="_blank" :href="item.file" v-else
             class="up-box-format">{{ item.file.substring(item.file.lastIndexOf('.') + 1) }}</a>
          <div style="width: 70%;padding: 4px;" class="secondary--text">
            نام فایل
            <br>
            {{ item.file.substring(item.file.lastIndexOf('/') + 1) }}
          </div>
          <div v-if="!hide" style="width: 15%">
            <!--            <v-progress-circular size="24" color="info" value="99" >
                          <span style="font-size: 8px" >36</span>
                        </v-progress-circular>-->
            <v-btn icon color="error">
              <v-icon color="error" size="22" @click="removeFile(i)">mdi-delete</v-icon>
            </v-btn>
          </div>
        </div>
      </v-col>
    </v-row>

    <v-row v-if="!hide" class="px-3 mb-3">
      <v-col class="pa-0" cols="2">
        <label v-if="!progress" style="cursor: pointer">
          <input v-if="!progress" type="file" ref="file" style="display: none" @change="onFileChange()">
          <div v-if="!progress" class="up-box-add">
            <v-icon small color="txtf" class="ml-1">mdi-plus</v-icon>
            <span class="txtf--text">افزودن فایل</span>
          </div>
        </label>

        <div v-if="progress" class="up-box-add">
          <v-progress-linear color="grey" height="100%" :value="progress">

            <template v-slot:default>
              <span style="font-size: 18px;" class="txtf--text">{{ progress }}</span>
            </template>

          </v-progress-linear>
        </div>

      </v-col>
    </v-row>

    <ImgViewDialog/>
  </div>
</template>


<script>
import ImgViewDialog from "@/components/Misc/ImgViewDialog";
import axios from "axios";

export default {
  props: ["items", "hide"],
  data() {
    return {
      progress: 0,
      files: this.items
    }
  },
  components: {
    ImgViewDialog
  },
  methods: {
    showImage(url) {
      this.$store.state.imgUrl = url;
      this.$store.state.imgDialog = true;
    },
    isImage(url) {
      return /\.(jpg|jpeg|png|webp|avif|gif|svg)$/.test(url);
    },
    removeFile(index) {
      this.files.splice(index, 1)
    },
    onFileChange() {
      const file = this.$refs.file.files[0];
      if (file.size > 10485760) {
        this.notify('حجم فایل بیشتر از 10 م.گ', 'error')
        return
      }
      let formData = new FormData();
      formData.append('file', file);
      axios.post(this.$store.state.host + '/projects/api/v1/files/create-files', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
        onUploadProgress: function (progressEvent) {
          this.progress = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total));
        }.bind(this)
      }).then(response => {
            this.files.push(response.data.data)
          }
      ).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.progress = 0;
      });
    },
  }
}
</script>


